import Vue from "vue";
import { PAYMENT_METHOD } from "@/vendor/enum";
import { $eventBus } from "../../vendor";

const createState = () =>
  Vue.observable({
    PAYMENT_METHOD,
    query: {
      amount: "",
      payment_method: [],
      split_status: "",
    },
    page: 1,
    pageSize: 20,
    list: [],
    loading: false,
    error: false,
    errorText: "加载失败，点击重试",
    finished: false,
  });

const state = createState();

const store = {
  state,
};

$eventBus.$on("root:exit-login", () => (store.state = createState()));

export default store;
