<template>
  <div class="transaction border_bottom_1px">
    <div class="user-indicator" v-if="dt.sellerIndicator">
      {{ dt.sellerIndicator.range }}日成单
      {{ dt.sellerIndicator.totalSuccessfulOrder }}
      &nbsp;|&nbsp;{{ dt.sellerIndicator.totalSuccessRate }}% &nbsp;
      <template v-if="dt.sellerIndicator.isSameCity == 1">
        |&nbsp; {{ dt.sellerIndicator.isSameCity == 1 ? "同城" : "" }}
      </template>
    </div>
    <div class="content">
      <van-image :src="$root.getImageUrl(dt.sellerAvatar)" class="avatar" round>
        <div slot="error" class="defaultAvatar size30"></div>
      </van-image>
      <div class="amountBox">
        <div class="amount">
          <div class="val">
            {{ parseFloat(dt.validAmount).toFixed(0) }}
            V币
          </div>
        </div>
      </div>
      <div class="payment-method">
        <div
          v-for="(item, index) in payments"
          :key="index"
          :class="`pay${item}`"
          class="item"
        />
      </div>
      <div class="btnWrapper">
        <van-button
          v-if="showBuyBtn"
          :to="`/buy/${dt.pendingOrderId}?m=${dt.sellerType === 'Business'  ? 1 : 0}`"
          class="btn"
        >
          购买
          <span class="split-status">
            ({{ dt.isSplit | orderSplitStatus("content") }})
          </span>
        </van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TransactionItem",
  props: {
    dt: {
      type: Object,
      default: () => ({
        avatar: "",
        amount: "0",
        paymentType: 1,
        isSplit: 1,
      }),
    },
  },
  computed: {
    payments() {
      return this.$root.parsePayments(this.dt.paymentType);
    },
    showBuyBtn() {
      return (this.dt.sellerType === 'Member' || this.dt.sellerType === 'Business')
              ? this.dt.sellerId !== this.$root.userInfo.userId
              : true;
    },
  },
};
</script>

