<template>
  <div class="page graybg page-transaction">
    <div class="tip_card">
      <div class="tips">
        <h3>交易须知</h3>
        <p>1.交易过程中实时留意订单状态，十分钟内完成</p>
        <p>2.购买时，付款成功后立即上传成功截图并点击「我已转账」</p>
        <p>3.出售时务必确定收到款项再进行打币</p>
        <p>4.订单出现异议第一时间点击右上角申请人工介入</p>
        <p>5.未按规定操作，平台不承担任何责任</p>
      </div>
    </div>
    <van-sticky offset-top="12.267vw">
      <van-dropdown-menu class="border_bottom_1px">
        <van-dropdown-item ref="amount" title="金额">
          <van-cell :border="false">
            <van-field
              v-model="query.amount"
              autocomplete="new-password"
              class="amount_field"
              placeholder="请输入总额"
            >
              <template #extra>
                <span> V币 </span>
              </template>
            </van-field>
          </van-cell>
          <van-cell class="btns">
            <van-button class="cancel" type="danger" @click="resetAmount">
              重置
            </van-button>
            <van-button class="confirm" type="primary" @click="load('amount')">
              确定
            </van-button>
          </van-cell>
        </van-dropdown-item>
        <van-dropdown-item ref="payment_method" title="交易方式">
          <van-cell :border="false" class="selections">
            <van-checkbox-group v-model="query.payment_method">
              <van-checkbox :name="PAYMENT_METHOD.BANK" class="btn">
                <van-button
                  slot="icon"
                  class="btn"
                  @click="
                    () => clearSelect('payment_method', PAYMENT_METHOD.BANK)
                  "
                >
                  银行卡转账
                </van-button>
              </van-checkbox>
              <van-checkbox :name="PAYMENT_METHOD.WE_CHAT" class="btn">
                <van-button
                  slot="icon"
                  class="btn"
                  @click="
                    () => clearSelect('payment_method', PAYMENT_METHOD.WE_CHAT)
                  "
                >
                  微信转账
                </van-button>
              </van-checkbox>
              <van-checkbox :name="PAYMENT_METHOD.ALI" class="btn">
                <van-button
                  slot="icon"
                  class="btn"
                  @click="
                    () => clearSelect('payment_method', PAYMENT_METHOD.ALI)
                  "
                >
                  支付宝转账
                </van-button>
              </van-checkbox>
            </van-checkbox-group>
          </van-cell>
          <van-cell class="btns">
            <van-button
              class="cancel"
              type="danger"
              @click="resetPaymentMethod"
            >
              重置
            </van-button>
            <van-button
              class="confirm"
              type="primary"
              @click="load('payment_method')"
            >
              确定
            </van-button>
          </van-cell>
          <div class="tip">仅显示可用的交易方式</div>
        </van-dropdown-item>
        <van-dropdown-item ref="split_status" title="是否可拆分">
          <van-cell :border="false" class="selections">
            <van-radio-group
              v-model="query.split_status"
              @change="(v) => onChange(v, 'split_status')"
            >
              <van-radio :name="1" class="btn">
                <van-button
                  slot="icon"
                  class="btn"
                  @click="() => clearSelect('split_status', 1, true)"
                >
                  可拆分
                </van-button>
              </van-radio>
              <van-radio :name="0" class="btn">
                <van-button
                  slot="icon"
                  class="btn"
                  @click="() => clearSelect('split_status', 0, true)"
                >
                  不可拆分
                </van-button>
              </van-radio>
            </van-radio-group>
          </van-cell>
        </van-dropdown-item>
      </van-dropdown-menu>
    </van-sticky>

    <van-pull-refresh v-model="loading" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :error-text="errorText"
        :error.sync="error"
        :finished="finished"
        finished-text="暂无更多数据"
        @load="search"
      >
        <TransactionItem
          v-for="(item, index) in list"
          :key="index"
          :dt="item"
        />
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { TransactionItem } from "./components";
import { marketPendingOrderList } from "@/apis/market";
import tools from "@/vendor/tools";
import { Dialog } from "vant";
import store from "./store";

export default {
  components: { TransactionItem },
  data() {
    return store.state;
  },
  watch: {
    query: {
      deep: true,
      handler() {
        this.page = 1;
      },
    },
  },
  created() {
    this.$root.updateUnReadCount();
  },
  methods: {
    onRefresh() {
      this.page = 1;
      this.list = [];
      this.loading = true;
      this.error = false;
      this.errorText = "加载失败，点击重试";
      this.finished = false;
      this.search();
    },
    search() {
      marketPendingOrderList(
        this.$tls.processParams(
          {
            page: this.page.toString(),
            pageSize: this.pageSize.toString(),
            amount: this.$tls.isEmpty(this.query.amount)
              ? ""
              : this.query.amount.toNumber(),
            isSplit: this.query.split_status.toString(),
            paymentType: this.query.payment_method
              .reduce((previous, current) => previous + current, 0)
              .toString(),
          },
          ["shake", "float"]
        )
      ).then(({ ok, data, msg }) => {
        this.loading = false;
        if (ok) {
          if (this.page == 1) {
            this.list = data.list || [];
          } else {
            this.list.push(...(data.list || []));
          }
          this.finished = this.page * this.pageSize >= data.total;
          this.page += 1;
        } else {
          if (msg === "必须先绑定银行卡才能交易") {
            Dialog.alert({
              message: "请先绑定支付方式！",
              className: "common",
              beforeClose: (action, done) => {
                this.$router.replace("/payments");
                done();
              },
            });
          }
          this.error = true;
          this.errorText = msg;
        }
      });
    },
    resetAmount() {
      this.page = 1;
      this.query.amount = "";
      this.search();
      this.$refs.amount.toggle(false);
    },
    resetPaymentMethod() {
      this.page = 1;
      this.query.payment_method = [];
      this.search();
      this.$refs.payment_method.toggle(false);
    },
    clearSelect(key, val, straight) {
      const selectedVal = this.query[key];
      if (selectedVal === val) {
        setTimeout(() => {
          this.query[key] = "";
          if (straight) {
            this.page = 1;
            this.search();
            this.$refs[key].toggle(false);
          }
        }, 50);
      }
    },
    load(ref) {
      this.$refs[ref].toggle(false);
      this.search();
    },
    onChange(v, ref) {
      if (tools.isNumber(v)) {
        this.page = 1;
        this.search();
        this.$refs[ref].toggle(false);
      }
    },
  },
};
</script>

